



































































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			userinf:{},
			static:''
		}
	},
	onLoad() {
		this.static=ut.static;
	},
	onShow() {		
		if(wx.getStorageSync('token')){
			this.req_userinfo();
		}else{
			this.userinf={};
		}
		
	},
	methods: {
		tel() {
			ut.call();
		},
		checkinf(){
			if(wx.getStorageSync('token')){
				this.go_mine_infchange();
				return;
			}
			this.go_mine_login();
		},
		go_mine_aboutus(){
			wx.navigateTo({
				url: '../mine/aboutus'
			})
		},
		go_mine_infchange(){
			if(!wx.getStorageSync('token')){
				this.go_mine_login();
				return;
			}
			wx.navigateTo({
				url: '../mine/infchange'
			})
		},
		go_mine_apply(type){
			if(!wx.getStorageSync('token')){
				this.go_mine_login();
				return;
			}
			wx.navigateTo({
				url: '../mine/apply?type='+type
			})
		},
		go_mine_login(){
			wx.navigateTo({
				url: '../mine/login'
			})
		},
		go_mine_adress(){
			if(!wx.getStorageSync('token')){
				this.go_mine_login();
				return;
			}
			wx.navigateTo({
				url: '../mine/adress'
			})
		},
		go_mine_securityCenter(){
			if(!wx.getStorageSync('token')){
				this.go_mine_login();
				return;
			}
			wx.navigateTo({
				url: '../mine/securityCenter'
			})
		},
		go_mine_problem(){
			wx.navigateTo({
				url: '../mine/problem'
			})
		},
		req_userinfo(){
			ut.request({
				url: "user/info",
				method:'get'
			}).then(data=>{
				this.userinf=data||{}
				wx.setStorageSync('userinf',data||{})
			})
		}
	}
}
